#header-nav{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 50px;
    margin-bottom: 5px;
    border-bottom: 1px solid $lines-color;
}
#header-nav div{
    display: flex;
    justify-content: space-between;
    width: 900px;
}
#header-nav div a{
    font-weight: bold;
    padding-bottom: 20px;
    border-bottom: 1px solid $lines-color;
}
#header-nav div a:hover{
    font-weight: bold;
    border-bottom: 1px solid #4691f6;
    color: #4691f6;
}