#products{
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 80px 0 80px 0;
}
#products h1{
    font-size: 25px;
    padding-bottom: 20px;
}
#products-itens{
    display: flex;
    justify-content: space-between;
    width: 900px;
    padding: 100px 0 100px 0;
    border-top: 2px solid $lines-color;
}
.products-itens-item{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 200px;
    height: 300px;
}
.products-itens-item-title{
    font-weight: bold;
    padding: 20px 0 20px 0;
}
.products-itens-item-price1{
    font-weight: bold;
    font-size: 12px;
    color: rgb(209, 158, 110);
    padding-bottom: 10px;
}
.products-itens-item-price2{
    font-size: 12px;
}