#news{
    display: flex;
    justify-content: center;
    padding: 100px 0 100px 0;
}
#news-container{
    display: flex;
    justify-content: space-between;
    width: 900px;
}
#news-container-item{
    width: 300px;
}
#news-container-item h1{
    font-size: 12px;
    padding: 15px 0;
    color: rgb(209, 158, 110);
}
#news-container-item p{
    font-weight: bold;
    font-family: $secundary-font;
    font-size: 16px;
}