#newsletter{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $lines-color;
    height: 100px;
    padding: 0 100px;
}
#newsletter-title{
    width: 300px;
}
#newsletter-form{
    display: flex;
}
#newsletter-form button{
    margin-left: 10px;
    padding: 10px 20px;
    background-color: #405a7c;
    color: #fff;
    border: none;
}