#footer-links{
    display: flex;
    justify-content: space-between;
    padding: 30px 0 30px 0;
    border-bottom: 2px solid $lines-color;
}
.footer-links-section{
    width: 200px;
}
.footer-links-section:first-child{
    padding-right: 50px;
}
.footer-links-section:last-child{
    padding-left: 50px;
}
.footer-links-section div{
    display: flex;
}
.footer-links-section a{
    display: block;
    padding: 10px 0;
}